import React, { useEffect, useState } from 'react';
import Archive from './Archive'; // استيراد النسخة المكتبية
import ArchiveMobile from './ArchiveMobile'; // استيراد النسخة المحمولة

const ArchiveContainer = () => {
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768); // تعيين الحالة الأولية بناءً على حجم النافذة

  const checkIsMobile = () => {
    const mobileView = window.innerWidth <= 768;
    if (mobileView !== isMobile) { // تحقق مما إذا كانت الحالة قد تغيرت
      setIsMobile(mobileView);
    }
  };

  useEffect(() => {
    window.addEventListener('resize', checkIsMobile); // إضافة مستمع لحدث تغيير الحجم

    return () => {
      window.removeEventListener('resize', checkIsMobile); // تنظيف مستمع الحدث عند فك التركيب
    };
  }, [isMobile]); // اعتماد على isMobile لتحسين الأداء

  return (
    <div>
      {isMobile ? <ArchiveMobile /> : <Archive />} {/* تبديل بين النسخة المحمولة والمكتبية */}
    </div>
  );
};

export default ArchiveContainer;
