import React, { useEffect, useRef, useState, useCallback } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import './ArchiveMobile.css';

// استيراد صور المشاريع
import website from './image/website.jpg';
import blockchain from './image/blockshain.jpg';
import offelia from './image/offelia.jpg';
import wooz from './image/wooz.jpg';
import ezdtech from './image/ezdtech.png';
import daypak from './image/daypak.jpg';
import medina from './image/medina.jpg';
import braintchy from './image/braintchy.jpg';

const projects = [
  { name: 'ANCIENT LIFE REVIVED', description: 'Research And Development, Unreal Engine', image: website },
  { name: 'BRAINTCHY', description: 'Bandana Physics Development, Inspiration, and Website Development', image: braintchy },
  { name: 'NFT BLOCKCHAIN', description: '3D Development on Blockchain', image: blockchain },
  { name: 'OEFELAE', description: 'Website Development, Payment Method Solutions, Marketing Assistance (In Progress)', image: offelia },
  { name: 'MEDINA', description: 'Web App: Blog Development with Django', image: medina },
  { name: 'WOOZCHAIN', description: 'Crypto Coin Flash Drive Prototype, User Experience and Simple Demo', image: wooz },
  { name: 'EZDTECH', description: 'Game Character Creation, Animation Development with Unreal Engine 4', image: ezdtech },
  { name: 'DAYPAK', description: '3D Product Fixes in Blender, Website Image Rotation', image: daypak },
];

const ArchiveMobile = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const containerRef = useRef(null);
  const itemRefs = useRef([]);
  const isScrolling = useRef(false);

  // دالة لمعالجة النقر على زر الاتصال
  const handleContactClick = useCallback(() => {
    const project = projects[currentIndex];
    const subject = `Inquiry About the "${project.name}" Project`;
    const body = `Dear [Your Name],\n\nI would like to inquire more details about the "${project.name}" project.\n\nBest regards,\n[Your Name]`;

    window.location.href = `mailto:${project.email}?cc=${project.cc}&subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
  }, [currentIndex]);

  // دالة لمعالجة النقر على المشروع
  const handleProjectClick = useCallback((index) => {
    setCurrentIndex(index);
  }, []);

  useEffect(() => {
    const container = containerRef.current;
    if (container) {
      const itemHeight = itemRefs.current[0]?.clientHeight || 0;
      container.scrollTo({
        top: currentIndex * itemHeight,
        behavior: 'smooth',
      });

      isScrolling.current = true;
      setTimeout(() => {
        isScrolling.current = false;
      }, 500);
    }
  }, [currentIndex]);

  // دالة لمعالجة التمرير
  const handleScroll = useCallback(() => {
    if (containerRef.current && !isScrolling.current) {
      const container = containerRef.current;
      const scrollPosition = container.scrollTop;
      const itemHeight = itemRefs.current[0]?.clientHeight || 0;
      const index = Math.round(scrollPosition / itemHeight);

      if (index !== currentIndex && index >= 0 && index < projects.length) {
        setCurrentIndex(index);
      }
    }
  }, [currentIndex]);

  useEffect(() => {
    const container = containerRef.current;
    if (container) {
      container.addEventListener('scroll', handleScroll);
      return () => {
        container.removeEventListener('scroll', handleScroll);
      };
    }
  }, [handleScroll]);

  return (
    <div className="archive-mobile-container" ref={containerRef}>
      <TransitionGroup>
        {projects.map((project, index) => (
          <CSSTransition
            key={index}
            timeout={300}
            classNames="project"
          >
            <div
              className={`project-item-mobile ${index === currentIndex ? 'active' : ''}`}
              onClick={() => handleProjectClick(index)}
              ref={(el) => itemRefs.current[index] = el}
            >
              <div className="project-name-mobile">{project.name}</div>
              <div className="project-description-mobile">{project.description}</div>
              <div className="project-image-mobile" style={{ backgroundImage: `url(${project.image})` }}></div>
              <button className="contact-button-mobile" onClick={handleContactClick}>
                <i className="fas fa-lock secure-icon"></i> Contact for Details
              </button>
            </div>
          </CSSTransition>
        ))}
      </TransitionGroup>
    </div>
  );
};

export default ArchiveMobile;
